import React, { Fragment, useEffect } from 'react'
import { CgMouse } from 'react-icons/cg'
import "./Home.css";
import ProductCard from "./ProductCard.js"
import MetaData from '../layout/Not Found/MetaData.js';
import {clearErrors, getProduct} from "../../actions/productAction.js"
import {useSelector,useDispatch} from "react-redux"
import Loader from "../layout/Loader/Loader.js"
import { useAlert } from "react-alert";

const Home = () => {
  console.log("Mini in Home");
  const alert=useAlert();
  const dispatch=useDispatch();
  const {loading,error,products}=useSelector((state)=>state.products)

  useEffect(()=>{
    if (error) {
     alert.error(error);
     dispatch(clearErrors());
    }

    dispatch(getProduct())
  },[dispatch,error,alert])
  return (
    <Fragment>
      {loading ?
        (<Loader/>) :(
          <Fragment>
      <MetaData title="Shoppers Hub"/>
    <div className="banner">
        <p>Welcome to Shopper's Hub</p>
        <h1>FIND AMAZING PRODUCTS BELOW</h1>

         <a href="#container">
        {/* scroll pe click krne pe direct container pe chle jayenge featured products wale */}
            <button>
                Scroll <CgMouse/>
            </button>
        </a>
    </div>

    <div className='homeHeading'>
    <h2>Featured Products</h2>
    </div>

    <div className='container' id="container">
      {products && products.map(product => (
        <ProductCard key={product._id} 
        product={product}/>
      ))}
    </div>

    
    </Fragment>
        ) }
    </Fragment>
  )
}

export default Home