import React from "react";
import "./aboutSection.css";
import { Button, Typography, Avatar } from "@material-ui/core";
import GithubIcon from "@material-ui/icons/GitHub";
import InstagramIcon from "@material-ui/icons/Instagram";
const About = () => {
  const visitLinkedIn = () => {
    window.location = "https://www.linkedin.com/in/falguni-shrivastav/";
  };
  return (
    <div className="aboutSection">
      <div></div>
      <div className="aboutSectionGradient"></div>
      <div className="aboutSectionContainer">
        <Typography component="h1">About Me</Typography>

        <div>
          <div>
            <Avatar
              style={{ width: "10vmax", height: "10vmax", margin: "2vmax 0" }}
              src="https://res.cloudinary.com/diz3i3940/image/upload/v1710577743/avatars/me_oh0znj.jpg"
              alt="Founder"
            />
            <Typography>Falguni Shrivastav</Typography>
            <Button onClick={visitLinkedIn} color="primary">
              Visit LinkedIn
            </Button>
            <span>
              This is an E-Commerce wesbite made by Falguni Shrivastav. Tech Stacks used
              are React , MongoDb , Express js, Node js.
            </span>
          </div>
          <div className="aboutSectionContainer2">
            <Typography component="h2">Profiles</Typography>
            <a
              href="https://github.com/falguni300103"
              target="blank"
            >
              <GithubIcon className="youtubeSvgIcon" />
            </a>

            <a href="https://instagram.com/falguniiii_.__" target="blank">
              <InstagramIcon className="instagramSvgIcon" />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;