import { useRef,useState,useEffect } from "react"
import React, { Fragment } from 'react'
import "./LoginSignUp.css";
import { Link } from "react-router-dom";
import  MailOutlineIcon  from "@material-ui/icons/MailOutline";
import  LockOpenIcon  from "@material-ui/icons/LockOpen";
import FaceIcon from "@material-ui/icons/Face";
import pic from "./Profile.jpg"
import Loader from "../layout/Loader/Loader"

import {useDispatch,useSelector} from "react-redux"
import {clearErrors,login,register} from "../../actions/userAction";
import {useAlert} from "react-alert";

const LoginSignUp = ({history,location}) => {
  const dispatch=useDispatch();
  const alert=useAlert();

  const {error,loading,isAuthenticated} = useSelector(state=>state.user)

  const loginTab=useRef(null);
  const registerTab=useRef(null);
  const switcherTab=useRef(null);

  const[loginEmail,setLoginEmail]=useState("");
  const[loginPassword,setLoginPassword]=useState("");

  const [user, setUser] = useState({
    name: "",
    email: "",
    password: "",
  });

  const { name, email, password } = user; //ye teeno chiz user se lelo..usern.name=name user.email=email user.password=password

  const [avatar, setAvatar] = useState(pic);
  const [avatarPreview, setAvatarPreview] = useState(pic);

  const loginSubmit = (e) => {
    e.preventDefault();
    dispatch(login(loginEmail,loginPassword))
  };

  const registerSubmit = (e) => {
    e.preventDefault();

    const myForm = new FormData();

    myForm.set("name", name);
    myForm.set("email", email);
    myForm.set("password", password);
    myForm.set("avatar", avatar);
    dispatch(register(myForm));//register action
  };

  const registerDataChange = (e) => {
    if (e.target.name === "avatar") {
      const reader = new FileReader();

      reader.onload = () => {
        if (reader.readyState === 2) {//loading state
          setAvatarPreview(reader.result);
          setAvatar(reader.result);
        }
      };

      reader.readAsDataURL(e.target.files[0]);
    } else {//change is due to name,email or password change
      setUser({ ...user, [e.target.name]: e.target.value });
    }
  };

  const redirect=location.search?location.search.split("=")[1]:"/account"
//location.search.split("=")[1]: If the condition is true (i.e., there is a query string),
// this expression splits the query string at the equal sign (=) and selects the second 
//element of the resulting array (index [1]). This typically represents the value associated with a 
//query parameter named after the equal sign. For example, if the URL is http://example.com?redirect=/home, 
//this expression would result in "/home".

  useEffect(()=>{
    if(error){
      alert.error(error);
      dispatch(clearErrors());
    }

    if(isAuthenticated){
      history.push(redirect);
    }
  },[dispatch,error,alert,history,isAuthenticated,redirect])


  const switchTabs=(e,tab)=>{
    if(tab==="login"){
      switcherTab.current.classList.add("shiftToNeutral");
      switcherTab.current.classList.remove("shiftToRight");

      registerTab.current.classList.remove("shiftToNeutralForm");
      loginTab.current.classList.remove("shiftToLeft");
    }
    if (tab === "register") {
      switcherTab.current.classList.add("shiftToRight");
      switcherTab.current.classList.remove("shiftToNeutral");

      registerTab.current.classList.add("shiftToNeutralForm");
      loginTab.current.classList.add("shiftToLeft");
    }
  }



  return (
    <Fragment>
    {loading?(
      <Loader/>
    ):(
      <Fragment>
       <div className="LoginSignUpContainer">
            <div className="LoginSignUpBox">
              <div>
                <div className="login_signUp_toggle">
                  <p onClick={(e) => switchTabs(e, "login")}>LOGIN</p>
                  <p onClick={(e) => switchTabs(e, "register")}>REGISTER</p>
                </div>
                <button ref={switcherTab}></button>
              </div>
              <form className="loginForm" ref={loginTab} onSubmit={loginSubmit}>
                <div className="loginEmail">
                  <MailOutlineIcon />
                  <input
                    type="email"
                    placeholder="Email"
                    required
                    value={loginEmail}
                    onChange={(e) => setLoginEmail(e.target.value)}
                  />
                </div>
                <div className="loginPassword">
                  <LockOpenIcon />
                  <input
                    type="password"
                    placeholder="Password"
                    required
                    value={loginPassword}
                    onChange={(e) => setLoginPassword(e.target.value)}
                  />
                </div>
                <Link to="/password/forgot">Forget Password ?</Link>
                <input type="submit" value="Login" className="loginBtn" />
              </form>
              <form
                className="signUpForm"
                ref={registerTab}
                encType="multipart/form-data"
                onSubmit={registerSubmit}
              >
                <div className="signUpName">
                  <FaceIcon />
                  <input
                    type="text"
                    placeholder="Name"
                    required
                    name="name"
                    value={name}
                    onChange={registerDataChange}
                  />
                </div>
                <div className="signUpEmail">
                  <MailOutlineIcon />
                  <input
                    type="email"
                    placeholder="Email"
                    required
                    name="email"
                    value={email}
                    onChange={registerDataChange}
                  />
                </div>
                <div className="signUpPassword">
                  <LockOpenIcon />
                  <input
                    type="password"
                    placeholder="Password"
                    required
                    name="password"
                    value={password}
                    onChange={registerDataChange}
                  />
                </div>

                <div id="registerImage">
                  <img src={avatarPreview} alt="Avatar Preview" />
                  <input
                    type="file"
                    name="avatar"
                    accept="image/*"
                    onChange={registerDataChange}
                  />
                </div>
                <input type="submit" value="Register" className="signUpBtn" />
              </form>
              </div>
              </div>
    </Fragment>
    )}
    </Fragment>
  )
}

export default LoginSignUp

//flow: pehle form ayega usko user fill krega frontend pe..fir submit button se registersubmit call hoga. ab yaha pe hmko data backend mein
//bhejna hai to pehle form se sara data lelo. ek user state variable bnega jiski properties name,email aur password hoga. ye teeno ko user se
//extract krlo fir ek form bnao aur ye sara data form mein bhrdo aur fir uss form ko user action call krk (register) dispatch krdo backend mein
//ab useraction call hua to userdata form k through ayega aur register request jayegi, then register success hone pe data api pe post hojayega
//and then redirect hojayega account page pe 